        // 要重新查询子公司的数据
        this.getZgsXsglTableData()
        this.getZgsXsfxTableData()
      } else if (type === 'sale-zgs-qsfx') {
        this.zgsQsfxIndex = index
        this.dealZgsQsfxLineData()
      }
    },
    changeAllType ({btn, index}) {
      // 当直营和子公司进行切换是，还原各个图标的子切换
      if (index === 0) {
        this.showZy = true
        this.zyXsfxIndex = 0
        this.zyPmfxIndex = 0
        this.zyQsfxIndex = 0
        this.dealZyBarLineData()
        this.dealZyPmfxTableData()
        this.dealZyQsfxLineData()
      } else {
        this.showZy = false
        this.zgsXsfxIndex = 0
        this.zgsDppmIndex = 0
        this.zgsQsfxIndex = 0
        this.dealZgsXsfxTableData()
        this.dealDppmTableData()
        this.dealZgsQsfxLineData()
      }
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'sale-zy-pmfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.pmfxTableData)
      } else if (type === 'sale-zgs-xsfx') {
        tempTable = _.cloneDeep(this.xsfxTableData)
        if (this.zgsXsfxIndex === 0) {
          tempTable.tableHeader = _.cloneDeep(this.xsfxTableData.tableHeader)
        } else {
          tempTable.tableHeader = _.cloneDeep(this.xsfxTableDataHeaderMon)
        }
      } else if (type === 'sale-zgs-dppm') {
        tempTable = _.cloneDeep(this.dppmTableData)
        if (this.zgsDppmIndex === 0) {
          tempTable.tableHeader = _.cloneDeep(this.dppmTableData.tableHeader)
        } else {
          tempTable.tableHeader = _.cloneDeep(this.dppmTableHeaderMon)
        }
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'sale-zy-pmfx') {
        // 这个来分辨哪个表格需要排序
        this.pmfxTableData.tableHeader = headerList
        this.pmfxTableData.tableContent = tableContent
      } else if (type === 'sale-zgs-xsfx') {
        this.xsfxTableData.tableHeader = headerList
        this.xsfxTableData.tableContent = tableContent
      } else if (type === 'sale-zgs-dppm') {
        this.dppmTableData.tableHeader = headerList
        this.dppmTableData.tableContent = tableContent
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      this.zgsQsfxTitle = '趋势分析-' + name
      this.zgsDppmTitle = '店铺排名-' + name
      if (this.zgsXsfxIndex === 0) {
        this.getZgsQsfxDayLineData(name)
      } else {
        this.getZgsQsfxLineData(name)
      }
      this.getZgsDppmTableData(name)
    },
