    },
    // 下面是查询数据的方法
    getAllData () {
      this.getZyXsfxBarLineData()
      this.getZyQsfxBarLineData()
      this.getZyPmfxTableData()
      this.getZgsXsglTableData()
      this.getZgsXsfxTableData()
    },
    // 直营销售分析的数据
    async getZyXsfxBarLineData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZyXsfxBarLineData, {dateStrM: this.dateStrM, dateStrJ: this.dateStrJ})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zyBarLineDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealZyBarLineData()
      }
    },
    dealZyBarLineData () {
      const list = []
      let dataList = []
      if (this.zyXsfxIndex === 0) {
        dataList = this.zyBarLineDataInit.resM || []
      } else if (this.zyXsfxIndex === 1) {
        dataList = this.zyBarLineDataInit.resJ || []
      } else if (this.zyXsfxIndex === 2) {
        dataList = this.zyBarLineDataInit.resNl || []
      } else if (this.zyXsfxIndex === 3) {
        dataList = this.zyBarLineDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.label = item.examination_subject
        obj.dc = initNum(item.yield_rate) * 100
        obj.tb = initNum(item.output_amt_yoy) * 100
        obj.jtdc = initNum(item.corp_yield_rate) * 100
        list.push(obj)
      }
      this.zyBarLineData = list
    },
    // 获取直营 趋势分析线图数据
    async getZyQsfxBarLineData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZyQsfxBarLineData, {dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zyQsfxLineDataInit = result.res
        this.dealZyQsfxLineData()
      }
    },
    dealZyQsfxLineData () {
      const list = []
      for (let item of this.zyQsfxLineDataInit) {
        const obj = {}
        obj.label = item.period_wid
        obj.dc = initNum(item.yield_rate) * 100
        if (this.zyQsfxIndex === 0) {
          obj.tb = initNum(item.output_amt_yoy) * 100
        } else {
          // 这里要绑定毛利率的值
          obj.tb = initNum(item.margin_rate) * 100
        }
        obj.nljdc = initNum(item.nl_yield_rate) * 100
        list.push(obj)
      }
      this.zyQsfxLineData = list
    },
    // 获取直营，排名分析表格数据
    async getZyPmfxTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZyPmfxTableData, {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.pmfxTableDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealZyPmfxTableData()
      }
    },
    dealZyPmfxTableData () {
      const list = []
      let dataList = []
      if (this.zyPmfxIndex === 0) {
        dataList = this.pmfxTableDataInit.resM || []
      } else if (this.zyPmfxIndex === 1) {
        dataList = this.pmfxTableDataInit.resJ || []
      } else if (this.zyPmfxIndex === 2) {
        dataList = this.pmfxTableDataInit.resNl || []
      } else if (this.zyPmfxIndex === 3) {
        dataList = this.pmfxTableDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.mb = formatNumber(initNum(item.target_amt).toFixed(0), 0)
        obj.xe = formatNumber(initNum(item.output_amt).toFixed(0), 0)
        obj.dc = switchPercent(item.yield_rate)
        obj.tb = switchPercent(item.output_amt_yoy)
        obj.mlv = switchPercent(item.margin_rate)
        obj.zb = switchPercent(item.rotio_rate)
        list.push(obj)
      }
      this.pmfxTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: [{name: '子公司', prop: 'zgs', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '目标', prop: 'mb', needOrder: true, order: '2', isPresent: false, needArrow: false},
          {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '达成', prop: 'dc', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}],
        tableContent: list.sort(compareSort('mb', false))
      }
    },
    // 获取子公司 销售概览 表格数据
    async getZgsXsglTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZgsXsglTableData, {dateStrD: this.dateStr, dateStrJ: this.dateStrJ})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.time = item.period_wid
          if (item.period_wid === '本日') {
            obj.mb = '--'
            obj.dc = '--'
            obj.tb = '--'
            obj.xe = formatNumber(initNum(item.output_amt).toFixed(0), 0)
            obj.mlv = switchPercent(item.margin_rate)
          } else {
            obj.mb = formatNumber(initNum(item.target_amt).toFixed(0), 0)
            obj.xe = formatNumber(initNum(item.output_amt).toFixed(0), 0)
            obj.dc = switchPercent(item.yield_rate)
            obj.tb = switchPercent(item.output_amt_yoy)
            obj.mlv = switchPercent(item.margin_rate)
          }
          list.push(obj)
        }
        this.xsglTableData.tableContent = list
      }
    },
    // 获取子公司 销售分析 表格数据
    async getZgsXsfxTableData () {
      // 这个接口和直营，排名分析公用一个接口
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZgsXsfxTableData, {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.xsfxTableDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealZgsXsfxTableData()
      }
    },
    dealZgsXsfxTableData () {
      const list = []
      let dataList = []
      if (this.zgsXsfxIndex === 0) {
        dataList = this.xsfxTableDataInit.resD || []
      } else if (this.zgsXsfxIndex === 1) {
        dataList = this.xsfxTableDataInit.resM || []
      } else if (this.zgsXsfxIndex === 2) {
        dataList = this.xsfxTableDataInit.resJ || []
      } else if (this.zgsXsfxIndex === 3) {
        dataList = this.xsfxTableDataInit.resNl || []
      } else if (this.zgsXsfxIndex === 4) {
        dataList = this.xsfxTableDataInit.resY || []
      }
      const headerDay = [
        {name: '子公司', prop: 'zgs', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ]
      const headerNotDay = this.xsfxTableDataHeaderMon
      if (this.zgsXsfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.zgs = item.distribution_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.tq = formatNumber(initNum(item.output_amt_lym), 2)
          obj.tb = switchPercent(item.output_amt_yoy)
          obj.pc = switchPercent(item.deviation)
          obj.mlv = switchPercent(item.margin_rate)
          obj.zb = switchPercent(item.rotio_rate)
          list.push(obj)
        }
      } else {
        for (let item of dataList) {
          const obj = {}
          obj.zgs = item.distribution_name
          obj.xe = formatNumber(initNum(item.output_amt).toFixed(0), 0)
          obj.mb = formatNumber(initNum(item.target_amt).toFixed(0), 0)
          obj.dc = switchPercent(item.yield_rate)
          obj.tb = switchPercent(item.output_amt_yoy)
          obj.mlv = switchPercent(item.margin_rate)
          obj.zb = switchPercent(item.rotio_rate)
          list.push(obj)
        }
      }
      this.xsfxTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: this.zgsXsfxIndex === 0 ? headerDay : headerNotDay,
        tableContent: this.zgsXsfxIndex === 0 ? list.sort(compareSort('xe', false)) : list.sort(compareSort('mb', false))
      }

      // 获取第一条数据，查询下面的线图和店铺表格数据
      if (this.xsfxTableData.tableContent.length > 0) {
        this.zgsQsfxTitle = '趋势分析-' + this.xsfxTableData.tableContent[0].zgs
        this.zgsDppmTitle = '店铺排名-' + this.xsfxTableData.tableContent[0].zgs
        // 这里要设置表格第一行默认选中
        this.$eventBus.$emit('resetSelectedRow', {type: 'sale-zgs-xsfx', name: this.xsfxTableData.tableContent[0].zgs})
        this.getZgsDppmTableData(this.xsfxTableData.tableContent[0].zgs)
      } else {
        this.zgsQsfxTitle = '趋势分析'
        this.zgsDppmTitle = '店铺排名'
        this.dppmTableData.tableContent = []
      }
      if (this.xsfxTableData.tableContent.length > 0 && this.zgsXsfxIndex === 0) {
        // 查询第一条子公司的日趋势图数据
        this.getZgsQsfxDayLineData(this.xsfxTableData.tableContent[0].zgs)
        this.getZgsDppmTableData(this.xsfxTableData.tableContent[0].zgs)
      } else if (this.xsfxTableData.tableContent.length > 0 && this.zgsXsfxIndex > 0) {
        this.getZgsQsfxLineData(this.xsfxTableData.tableContent[0].zgs)
        this.getZgsDppmTableData(this.xsfxTableData.tableContent[0].zgs)
      } else if (this.xsfxTableData.tableContent.length === 0 && this.zgsXsfxIndex === 0) {
        this.zgsLineData = []
      } else if (this.xsfxTableData.tableContent.length === 0 && this.zgsXsfxIndex > 0) {
        this.zgsQsfxLineData = []
      }
    },
    // 获取子公司 趋势分析日数据
    async getZgsQsfxDayLineData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZgsQsfxDayLineData, {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM, subName: companyName})
