          obj.sj = initNum(item.yield_rate) * 100
          obj.mb = initNum(item.target_yield_rate) * 100
          list.push(obj)
        }
        if (list.length > 0) {
          this.zgsLineData = list
        } else {
          this.zgsLineData = []
        }
      }
