    // 获取子公司店铺排名 表格数据
    async getZgsDppmTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.sale.getZgsDppmTableData, {dateNum: getMonthDayNum(this.dateStr), subName: companyName, dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ})
      if (Object.is(__statusCode, '1')) {
