      }
    },
    dealDppmTableData () {
      const list = []
      let dataList = []
      if (this.zgsDppmIndex === 0) {
        dataList = this.dppmTableDataInit.resD || []
      } else if (this.zgsDppmIndex === 1) {
        dataList = this.dppmTableDataInit.resM || []
      } else if (this.zgsDppmIndex === 2) {
        dataList = this.dppmTableDataInit.resJ || []
      } else if (this.zgsDppmIndex === 3) {
        dataList = this.dppmTableDataInit.resNl || []
      } else if (this.zgsDppmIndex === 4) {
        dataList = this.dppmTableDataInit.resY || []
      }
      const headerDay = [
        {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ]
      const headerNotDay = this.dppmTableHeaderMon
      if (this.zgsDppmIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.dp = item.customer_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.tq = formatNumber(initNum(item.output_amt_lym), 2)
          obj.tb = switchPercent(item.output_amt_yoy)
          obj.mlv = switchPercent(item.margin_rate)
          obj.pc = switchPercent(item.deviation)
          obj.zb = switchPercent(item.rotio_rate)
          list.push(obj)
        }
      } else {
        for (let item of dataList) {
          const obj = {}
          obj.dp = item.customer_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.mb = formatNumber(initNum(item.target_amt), 2)
          obj.tb = switchPercent(item.output_amt_yoy)
          obj.dc = switchPercent(item.yield_rate)
          obj.mlv = switchPercent(item.margin_rate)
          obj.zb = switchPercent(item.rotio_rate)
          list.push(obj)
        }
      }
      if (this.zgsDppmIndex === 0) {
        this.dppmTableData.tableHeader = headerDay
        this.dppmTableData.tableContent = list.sort(compareSort('xe', false))
      } else {
        this.dppmTableData.tableHeader = headerNotDay
        this.dppmTableData.tableContent = list.sort(compareSort('mb', false))
      }
    }
  },
  created () {
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.commit('updateHeaderTitle', '销售')
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '总监', URL: '销售'})
    this.$store.commit('updateShowIcon', true)
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  components: {
    pageTitle,
    SwitchBtnList,
    MinTitle,
    BarLineChart,
    TableList
    // Tabs
  }
}
</script>

